




















































import {request, getQueryString} from 'common/js/common'
import {getAdvList} from 'common/js/requestUrl'

export default {
	data() {
		return {
			latitude: '',
			longitude: '',
			mapContext: {},
			callout: {
				bgColor: 'red'
			},
			covers: [],
			list: [],
			mainItem: {},
			id: '',
			type: ''
		};
	},
	onShow(){
		this.mapContext = uni.createMapContext('map',this)
		uni.getLocation({
		  type: 'gcj02',
		  success: res => {
			this.latitude = res.latitude
			this.longitude = res.longitude
			this.covers.push({
				id: 0,
				latitude: this.latitude,
				longitude: this.longitude,
				iconPath: '/static/img/icon/Location.png',
				width: '60rpx',
				height: '60rpx',
				callout:{
					content: '我的位置',
					display: 'ALWAYS',
					padding: 10,
					x: -40,
					y: -40,
					bgColor: '#fff',
					borderRadius: 10
				},
			})
		  }
		})
	},
	onLoad(options){
		this.getAdvList()

		let link = decodeURIComponent(options.q)
		this.changeData(link)
	},
	methods: {
		changeData(link){
			let paramArr = link.split('=');
			if (paramArr.length == 2){
				var params = paramArr[1].split('_');
				this.id = params[0];
				this.type =params[1];
			}
		},
		changePosition(){
			this.mapContext.moveToLocation()
		},
		openScan(){
			uni.scanCode({
				onlyFromCamera: true,
				success: (res) => {
					this.changeData(res.result)
				}
			});
		},
		navicateToPath(url){
			uni.navigateTo({
				url: '/'+ url
			});
		},
		chooseLocation(){
			uni.openLocation({
				latitude: 27.9539300000,
				longitude: 120.5989070000,
				success: function () {
					console.log('success');
				}
			});
		},
		getAdvList(){
			request(getAdvList, {},(res)=> {
				if(res.code == 200){
					this.list = res.result.data
					res.result.data.forEach((v) => {
						this.covers.push({
							id: v.id,
							latitude: v.latitude,
							longitude: v.longitude,
							iconPath: '/static/img/icon/Location.png',
							width: '60rpx',
							height: '60rpx',
							callout:{
								content: v.title,
								display: 'ALWAYS',
								padding: 10,
								x: -40,
								y: -40,
								bgColor: '#fff',
								borderRadius: 10
							},
						})
					})
				}
			})
		},
	}
};
